<template>
  <div class="shop">
    <h2>–ú–∞–≥–∞–∑–∏–Ω</h2>
    <p>–£ –≤–∞—Å {{ coins }} –º–æ–Ω–µ—Ç.</p>
    <button @click="buyItem" class="shop-button">–ö—É–ø–∏—Ç—å –ø—Ä–µ–¥–º–µ—Ç –∑–∞ 5 –º–æ–Ω–µ—Ç</button>
  </div>
</template>

<script>
import { inject } from 'vue';

export default {
  name: 'ShopComponent',
  setup() {
    const coins = inject('coins');

    const buyItem = () => {
      if (coins.value >= 5) {
        coins.value -= 5; // –°–Ω–∏–∂–∞–µ–º –º–æ–Ω–µ—Ç—ã –Ω–∞ 5
        alert('–í—ã –∫—É–ø–∏–ª–∏ –ø—Ä–µ–¥–º–µ—Ç!');
      } else {
        alert('–ù–µ–¥–æ—Å—Ç–∞—Ç–æ—á–Ω–æ –º–æ–Ω–µ—Ç –¥–ª—è –ø–æ–∫—É–ø–∫–∏!');
      }
    };

    return {
      coins,
      buyItem,
    };
  },
};
</script>

