<template>
  <div>
    <h2>User Profile</h2>
    <div v-if="loading">–ó–∞–≥—Ä—É–∑–∫–∞...</div>
    <div v-else-if="error">{{ error }}</div>
    <div v-else>
      <p>–ù–∏–∫–Ω–µ–π–º: {{ username }}</p>
    </div>
  </div>
</template>

<script>
import { getUsernameById } from '@/services/userService';

export default {
  name: 'UserProfile',
  props: {
    userId: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      username: '',
      loading: false,
      error: '',
    };
  },
  created() {
    this.fetchUsername(); // –í—ã–∑—ã–≤–∞–µ–º fetchUsername –≤ –º–æ–º–µ–Ω—Ç —Å–æ–∑–¥–∞–Ω–∏—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞
  },
  methods: {
    async fetchUsername() {
      this.loading = true;
      this.error = '';
      const userId = window.Telegram.WebApp.initDataUnsafe.user.id; // –ü–æ–ª—É—á–∞–µ–º userId –∏–∑ Telegram Web App

      try {
        this.username = await getUsernameById(userId); // –ü–æ–ª—É—á–∞–µ–º –Ω–∏–∫–Ω–µ–π–º –ø–æ userId
      } catch (err) {
        this.error = '–ù–µ —É–¥–∞–ª–æ—Å—å –∑–∞–≥—Ä—É–∑–∏—Ç—å –Ω–∏–∫–Ω–µ–π–º –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è.';
        console.error(err);
      } finally {
        this.loading = false;
      }
    },
  },
};
</script>

